body,html
{
  width: 100%;
  overflow-x: hidden;
  background-color: unset;
}
@font-face{
  font-family: bebas;
  src : url("./fonts/BEBASNEUE-REGULAR.OTF");
}
@font-face{
  font-family: erawan;
  src : url("./fonts/DB ERAWAN X.TTF");
}
@font-face{
  font-family: helvethaica;
  src : url("./fonts/DB HELVETHAICA X BD EXT.TTF");
}
@font-face{
  font-family: saraban;
  src : url("./fonts/THSARABUNNEW.TTF");
}
.bebas{
  font-family: bebas;
}
nav{
  font-size: 30px;
  background-color: #F5CC76;
  height: 91px;
  position: fixed;
  z-index: 20;
}
nav ul{
  margin-top: 20px;
  margin-left: 53%;
  display: flex;
  flex-direction: row;
  width: 35%;
  justify-content: space-between;
  background-color: #F5CC76;
  position: fixed;
  z-index: 20;
}
nav li{
  list-style: none;
  z-index: 20;
}
.nav-bg
{
  position: fixed;
  height: 91px;
  width: 100%;
  background-color: #F5CC76;
  z-index: 20;
}
