product{
  text-align: center;
  font-family: bebas;
}
product h1{
  margin-left: 8%;
  font-size: 72px;
  text-align: left;
}
.product-grid
{
  font-family: helvethaica;
  display: grid;
  width: 84%;
  margin: auto;
  grid-template-columns: 4fr 17fr;
  column-gap: 20px;
  margin-bottom: 100px;
}
.product-type-selected
{
  font-size: 42px;
  background-color: #583C2E;
  width: 266px;
  padding: 22px 20px;
  color: white;
  border-radius: 30px;
  margin-top: 20px;
  line-height: 42px;
  height: 128px;
  cursor: pointer;
}
.product-type
{
  cursor: pointer;
  font-size: 42px;
  background-color: white;
  width: 266px;
  padding: 22px 20px;
  color: #583C2E;
  border-radius: 30px;
  margin-top: 20px;
  line-height: 42px;
  height: 128px;
  border: #583C2E solid 4px;
}
.product-type:hover
{
  font-size: 42px;
  background-color: #583C2E;
  width: 266px;
  padding: 22px 20px;
  color: white;
  border-radius: 30px;
  margin-top: 20px;
  line-height: 42px;
  height: 128px;
  transition: 0.2s;
}
.product-size-box
{
  box-shadow: 0 0 10px gray;
  border-radius: 30px;
  width: 100%;
  height: 865px;
  margin-top: 20px;
  margin-bottom: 10px;
  padding: 50px;
}
.product-size-box h1
{
  text-align: center;
  margin-left: 0;
}
.product-size-box h2
{
  font-family: saraban;
  text-align: center;
  font-size: 36px;
}
.product-size-grid
{
  display: grid;
  grid-template-columns: 50fr 1fr 50fr;
  width: 100%;
  margin: auto;
  column-gap: 50px;
}
.product-size img{
  border-radius: 30px;
  width: 200px;
  border: #583C2E solid 5px;
  border-bottom: #583C2E solid 40px;
  z-index: -1;
}
.product-size h2{
  font-size: 28px;
  color: white;
  
}
.product-item-grid{
  margin-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 0px;
  row-gap: 40px;
}
.vertical-line
{
  margin-top: 30px;
  height: 600px;
  width: 4px;
  background-color: gray;
}
.packaging
{
  width: 100%;
  background-color: #F5DAA3;
  padding: 80px;
}
.packaging h1{
  font-family: bebas;
  font-size: 72px;
  margin-left: 8%
}
.packaging img{
  border-radius: 30px;
  width: 300px;
  border: #583C2E solid 5px;
  border-bottom: #583C2E solid 40px;
  z-index: 1;

}
.packaging-flex{
  display: flex;
  justify-content: space-between;
  width: 84%;
  margin: auto;
  margin-top: 40px;
  margin-bottom: 50px;
}
